.bakery__container {
  width: 100%;
  padding: 0 60px;

  @include breakpoint($notebook-bp) {
    padding: 0 40px;
  
    & .cookies:nth-child(2n) .cookies__column {
      padding-left: 30px;

      @include breakpoint($small-tablet-bp) {
        padding: 0;
      }
    }
  }

  @include breakpoint($mobile-bp) {
    padding: 0 20px;
  }

  &-more {
    display: none;

    @include breakpoint($notebook-bp) {
      padding: 0 40px;
    
      & .cookies:nth-child(2n) .cookies__column {
        padding-left: 30px;
  
        @include breakpoint($small-tablet-bp) {
          padding: 0;
        }
      }
    }

    &--active {
      display: block;
    }
  }
}

.bakery {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1440px;
  padding-bottom: 80px;
  margin: 0 auto;
  background-color: #fff;
  transition: 1s;

  @include breakpoint($mobile-bp) {
    padding-bottom: 40px;
  }

  &__title h2 {
    margin: 27px 0;

    @include breakpoint($mobile-bp) {
      font-size: 60px;
      line-height: 60px;
    }
  }

  &__button {
    @extend %main-button;

    &:after {
      content: "All bakeries";
    }

    &--active {
      &:after {
        content: "Close bakeries";
      }
    }
  }
}
