.image-change {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 400px;
  justify-content: space-between;

  &__left {
    background-image: url(/images/left.png);
  }

  &__rigth {
    background-image: url(/images/rigth.png);
  }

  &__left,
  &__rigth {
    width: 15px;
    height: 26px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.4s;

    @include breakpoint($mobile-bp) {
      height: 17px;
    }

    &:hover {
      transform: scale(1.3);
      transition: 0.4s;
      cursor: pointer;
    }
  }

  &__down {
    background-image: url(/images/down.png);
  }

  &__up {
    background-image: url(/images/up.png);
  }

  &__up,
  &__down {
    height: 15px;
    width: 26px;
    padding: 10px;
    margin-right: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.4s;

    @include breakpoint($mobile-bp) {
      height: 12px;
    }

    &:hover {
      transform: scale(1.5);
      transition: 0.4s;
      cursor: pointer;
    }
  }

  &__num,
  &__numCopy {
    padding-right: 9px;
    font-size: 65px;
    color: $alter-color;
    cursor: pointer;
    transition: 0.4s;
    
    @include breakpoint($mobile-bp) {
      font-size: 40px;
    }

    &--1 {
      font-size: 150px;
      color: black;

      @include breakpoint($tablet-bp) {
        font-size: 100px;
      }

      @include breakpoint($mobile-bp) {
        font-size: 55px;
      }
    }
  }

  &__num2,
  &__num2Copy {
    font-size: 65px;
    transition: 0.4s;
    cursor: pointer;
    color: $alter-color;
    padding-right: 9px;

    @include breakpoint($mobile-bp) {
      font-size: 40px;
    }

    &--2 {
      font-size: 150px;
      color: black;

      @include breakpoint($tablet-bp) {
        font-size: 100px;
      }

      @include breakpoint($mobile-bp) {
        font-size: 55px;
      }
    }
  }

  &__num3,
  &__num3Copy {
    transition: 0.4s;
    cursor: pointer;
    font-size: 65px;
    color: $alter-color;
    padding-right: 9px;

    @include breakpoint($mobile-bp) {
      font-size: 40px;
    }

    &--3 {
      font-size: 150px;
      color: black;

      @include breakpoint($tablet-bp) {
        font-size: 100px;
      }

      @include breakpoint($mobile-bp) {
        font-size: 55px;
      }
    }
  }

  @include breakpoint($tablet-bp) {
    display: none;
  }

  &-mb {
    display: none;
    position: absolute;
    flex-direction: row;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);

    @include breakpoint($tablet-bp) {
      display: flex;
      bottom: 80px;
      gap: 18px;
    }

    @include breakpoint($mobile-bp) {
      display: flex;
      bottom: 25px;
      gap: 18px;
    }
  }
}
